<template>
  <div class="hello">
    {{ msg }}
    <form @submit.prevent="">
      <input type="file" ref="imgs" />
      <button @click="upload">上传文件</button>
    </form>
    <img :src="imgUrl" class="img" />
  </div>
</template>

<script>
import * as qiniu from 'qiniu-js';
import axios from 'axios';
import { BASE_IMAGE_URL } from '../constant';

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      imgUrl: ''
    };
  },
  methods: {
    upload() {
      // 模拟获取uploadToken
      const p = new Promise(resolve => {
        axios
          .get('http://localhost:8080/uptoken')
          .then(({ data: { token } }) => {
            resolve(token);
          });
      });
      p.then(token => {
        const file = this.$refs.imgs.files[0];
        console.log('file:', file);

        const { name } = file; // 上传到服务器中文件的名字, 这里不进行处理
        const config = {
          useCdnDomain: false // 启用cdn加速域名
        };
        const putExtra = {
          fname: name, // 文件原来的名字: 就是file.name
          // params: {}, // 存放自定义变量
          mimeType: null // null | array, 例如['image/png']
        };
        const observer = qiniu.upload(file, name, token, putExtra, config);
        console.log('observer', observer);

        // eslint-disable-next-line no-unused-vars
        const subscription = observer.subscribe({
          next(res) {
            console.log('next:', res);
            if (res.total.percent === 100) {
              console.log('done');
            }
          },
          error(err) {
            console.log('error:');
            console.dir(err);
          },
          complete({ key }) {
            // console.log('complete:', res);
            this.imgUrl = `${BASE_IMAGE_URL}${key}`;
          }
        });

        // subscription.unsubscribe() // 停止当前文件上传
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.img {
  border: 1px solid black;
}
</style>
